.like {
    font-size: 18px;
    background-color:  rgb(173, 2, 150);
    color: var(--primary-color);
    border: 0;
    border-radius: 50%;
    width: 55px;
    height: 55px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 16px;
    right: 16px;
    z-index: 10;
}


/* ERROR */
.error {
    color: var(--secondary-color);
    font-size: 1.5em;
    margin: 0;
    text-align: center;
}

.skeleton {
    user-select: none;
    background-color: #eeeeee;
    color: #eeeeee;
}`, "",{"version":3,"sources":["webpack://./src/styles/asset.css"],"names":[],"mappings":"AAAA,gBAAgB;AAChB;IACI,eAAe;IACf,mCAAmC;IACnC,2BAA2B;IAC3B,SAAS;IACT,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,YAAY;IACZ,WAAW;IACX,WAAW;AACf;;;AAGA,UAAU;AACV;IACI,6BAA6B;IAC7B,gBAAgB;IAChB,SAAS;IACT,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,yBAAyB;IACzB,cAAc;AAClB","sourcesContent":["/* LIKE BUTOON */\r\n.like {\r\n    font-size: 18px;\r\n    background-color:  rgb(173, 2, 150);\r\n    color: var(--primary-color);\r\n    border: 0;\r\n    border-radius: 50%;\r\n    width: 55px;\r\n    height: 55px;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    position: fixed;\r\n    bottom: 16px;\r\n    right: 16px;\r\n    z-index: 10;\r\n}\r\n\r\n\r\n/* ERROR */\r\n.error {\r\n    color: var(--secondary-color);\r\n    font-size: 1.5em;\r\n    margin: 0;\r\n    text-align: center;\r\n}\r\n\r\n.skeleton {\r\n    user-select: none;\r\n    background-color: #eeeeee;\r\n    color: #eeeeee;\r\n}"],"sourceRoot":""}]);
